

import { Http, Response, Headers, RequestOptions } from '@angular/http';
import {MdSnackBar,MdDialog,MdDialogConfig,MdDialogRef} from '@angular/material';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import { 
    Component,
    trigger,
    state,
    style,
    transition,
    animate,
    keyframes,
    Output,
    EventEmitter,

 } from '@angular/core';




@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
    styleUrls: ['./app.component.css'], 

})
export class AppComponent {

  model = new Filter(null,null,'');
  data:Register[];
 
  constructor(private http: Http,public snackBar: MdSnackBar,public dialog: MdDialog) { }
 search(){
	
  return	new Promise((resolve) => {
					this.http.post("/home/getdata",this.model).subscribe((data) => {
					if(data.json()) {

						resolve(data.json());
						var enq = data.json();
						if(enq.code == 400){
						this.openSnackBar("Transaction not found.","ok");
						
					  }

						

						if(enq.code == 200){
							console.log(enq.data);
							this.data = enq.data;

						}
						

					}
					
					else{
						console.log("Error");
					}
				}
			)
		});
  }

   openDialog(r) {

    return	new Promise((resolve) => {
					this.http.get("/home/GetDeatilData?id=" + r.id).subscribe((data) => {
					if(data.json()) {

						resolve(data.json());
						var enq = data.json();
						if(enq.code == 400){
						this.openSnackBar("Transaction not found.","ok");
						
					  }

						

						if(enq.code == 200){
							

							let config = new MdDialogConfig();
							let dialogRef:MdDialogRef<RegisterDialog> = this.dialog.open(RegisterDialog, config);
							dialogRef.componentInstance.reg = enq.data;
							console.log(enq);

						}
						

					}
					
					else{
						console.log("Error");
					}
				}
			)
		});



    
  }

  	openSnackBar(message: string, action: string) {
						this.snackBar.open(message, action, {
						  duration: 2000,
						});
						}

}

@Component({
  selector: 'register-dialog',
  templateUrl: './dialog.html',
})
export class RegisterDialog {
reg:Register;
remark:string;
constructor(private http: Http,public snackBar: MdSnackBar,public dialogRef: MdDialogRef<RegisterDialog>) { 


}

	openSnackBar(message: string, action: string) {
						this.snackBar.open(message, action, {
						  duration: 2000,
						});
						}

approve(id,s){
	let r = new Review(id,s,this.remark);
	 return	new Promise((resolve) => {
					this.http.post("/home/Review",r).subscribe((data) => {
					if(data.json()) {

						resolve(data.json());
						var enq = data.json();
						if(enq.code == 400){
						this.openSnackBar("Transaction saving fail.","ok");
						
					  }

						

						if(enq.code == 200){
							this.openSnackBar("Transaction save sucessfully.","ok");

						}
						

					}
					
					else{
						console.log("Error");
					}
				}
			)
		});
}



}
export class Review{
constructor(
    public id: number,
	
	public status:string ,
	public remark: string,
	){}
	
}

export class Register {

  constructor(
    public id: number,
    public fname: string,
	public lname: string,
	public dob: string,
	public mobile: string,
	public email: string,
	public city: string,
    public state: string,
	public address: string,
    public postal: string,
	
	public photo: string,
	public nrc1: string,
	public nrc2: string,
	public services : string[]

  ) {  }
}

export class Filter{
	constructor(
	
		public start:Date,
		public end:Date,
		public refno:string
	){}
}





