import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule } from '@angular/forms';

import { MaterialModule } from '@angular/material';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';

import { ImageUploadModule } from 'angular2-image-upload';
import {MdNativeDateModule,MdDatepickerModule} from '@angular/material';
import 'hammerjs';
import {Http,HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import { RegisterDialog } from './app.component';


@NgModule({
  declarations: [
    AppComponent,
	RegisterDialog
  ],
  entryComponents: [RegisterDialog],
  imports: [
  BrowserModule,BrowserAnimationsModule,MaterialModule,MdNativeDateModule,MdDatepickerModule, FormsModule,ImageUploadModule.forRoot(),HttpModule
  ],
 
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



